import {Col, Row} from 'bee-layout';
import {Panel} from 'bee-panel';
import Button from 'bee-button';
import React, {Component} from 'react';
import ReactDOM from 'react-dom';


const CARET = <i className="uf uf-arrow-down"></i>;

const CARETUP = <i className="uf uf-arrow-up"></i>;


var Demo1 = require("./demolist/Demo1");var Demo2 = require("./demolist/Demo2");var Demo3 = require("./demolist/Demo3");var Demo4 = require("./demolist/Demo4");var Demo5 = require("./demolist/Demo5");var Demo6 = require("./demolist/Demo6");var DemoArray = [{"example":<Demo1 />,"title":" Menu","code":"/**\r\n * @title Menu\r\n * @description \r\n */\r\n\r\nimport React, { Component } from 'react';\r\nimport { Menu } from 'tinper-bee';\r\n\r\nconst SubMenu = Menu.SubMenu;\r\nconst MenuItemGroup = Menu.ItemGroup;\r\n\r\n\r\nclass Demo1 extends Component {\r\n    constructor(props, context) {\r\n        super(props, context);\r\n        this.state = {\r\n            current: 'mail'\r\n        }\r\n    }\r\n\r\n    handleClick = (e) => {\r\n        this.setState({\r\n            current: e.key,\r\n        });\r\n    }\r\n\r\n    render() {\r\n        return (\r\n            <Menu onClick={this.handleClick}\r\n                  selectedKeys={[this.state.current]}\r\n                  mode=\"horizontal\"\r\n                >\r\n                <Menu.Item key=\"mail\" attribute={{'type': 'mail'}}>\r\n                     1\r\n                </Menu.Item>\r\n                <Menu.Item key=\"app\" disabled>\r\n                     2\r\n                </Menu.Item>\r\n                <SubMenu title={<span> 1 - </span>}>\r\n                    <MenuItemGroup title=\" 1\">\r\n                        <Menu.Item key=\"setting:1\"> 1</Menu.Item>\r\n                        <Menu.Item key=\"setting:2\"> 2</Menu.Item>\r\n                    </MenuItemGroup>\r\n                    <MenuItemGroup title=\" 2\">\r\n                        <Menu.Item key=\"setting:3\"> 3</Menu.Item>\r\n                        <Menu.Item key=\"setting:4\"> 4</Menu.Item>\r\n                    </MenuItemGroup>\r\n                </SubMenu>\r\n            </Menu>\r\n        )\r\n    }\r\n}\r\n\r\n","desc":" "},{"example":<Demo2 />,"title":" Menu","code":"/**\r\n * @title Menu\r\n * @description \r\n */\r\n\r\nimport React, { Component } from 'react';\r\nimport { Menu } from 'tinper-bee';\r\n\r\nconst SubMenu = Menu.SubMenu;\r\nconst MenuItemGroup = Menu.ItemGroup;\r\n\r\n\r\nclass Demo2 extends Component {\r\n    constructor(props, context) {\r\n        super(props, context);\r\n        this.state = {\r\n            current: 1\r\n        }\r\n    }\r\n\r\n    handleClick = (e) => {\r\n\r\n        this.setState({\r\n            current: e.key,\r\n        });\r\n    }\r\n\r\n    render() {\r\n        return (\r\n            <Menu theme=\"dark\" onClick={this.handleClick} style={{ width: 240 }} defaultOpenKeys={['demo3sub1']} selectedKeys={[this.state.current]} mode=\"inline\">\r\n                <SubMenu key=\"demo3sub1\" title={<span><span> 1</span></span>}>\r\n                    <MenuItemGroup title=\" 1\">\r\n                        <Menu.Item key=\"1\"> 1</Menu.Item>\r\n                        <Menu.Item key=\"2\"> 2</Menu.Item>\r\n                    </MenuItemGroup>\r\n                    <MenuItemGroup title=\" 2\">\r\n                        <Menu.Item key=\"3\"> 3</Menu.Item>\r\n                        <Menu.Item key=\"4\"> 4</Menu.Item>\r\n                    </MenuItemGroup>\r\n                </SubMenu>\r\n                <SubMenu key=\"demo3sub2\" title={<span><span> 2</span></span>}>\r\n                    <Menu.Item key=\"5\"> 5</Menu.Item>\r\n                    <Menu.Item key=\"6\"> 6</Menu.Item>\r\n                    <SubMenu key=\"demo3sub3\" title=\"\">\r\n                        <Menu.Item key=\"7\"> 7</Menu.Item>\r\n                        <Menu.Item key=\"8\"> 8</Menu.Item>\r\n                    </SubMenu>\r\n                </SubMenu>\r\n                <SubMenu key=\"demo3sub4\" title={<span><span> 3</span></span>}>\r\n                    <Menu.Item key=\"9\"> 9</Menu.Item>\r\n                    <Menu.Item key=\"10\"> 10</Menu.Item>\r\n                    <Menu.Item key=\"11\"> 11</Menu.Item>\r\n                    <Menu.Item key=\"12\"> 12</Menu.Item>\r\n                </SubMenu>\r\n            </Menu>\r\n        )\r\n    }\r\n}\r\n\r\n","desc":" "},{"example":<Demo3 />,"title":" Menu","code":"/**\r\n * @title Menu\r\n * @description \r\n */\r\n\r\n\r\nimport React, { Component } from 'react';\r\nimport { Menu } from 'tinper-bee';\r\n\r\nconst SubMenu = Menu.SubMenu;\r\n\r\n\r\nclass Demo3 extends Component {\r\n    constructor(props, context) {\r\n        super(props, context);\r\n        this.state = {\r\n            current: '1',\r\n            openKeys: []\r\n        }\r\n    }\r\n    handleClick = (e) => {\r\n        console.log('Clicked: ', e);\r\n        this.setState({current: e.key});\r\n    }\r\n    onOpenChange = (openKeys) => {\r\n        const state = this.state;\r\n\r\n        const latestOpenKey = this.myfilter(openKeys,state.openKeys);\r\n        const latestCloseKey = this.myfilter(state.openKeys,openKeys);\r\n\r\n        let nextOpenKeys = [];\r\n        if (latestOpenKey) {\r\n            nextOpenKeys = this.getAncestorKeys(latestOpenKey).concat(latestOpenKey);\r\n        }\r\n        if (latestCloseKey) {\r\n            nextOpenKeys = this.getAncestorKeys(latestCloseKey);\r\n        }\r\n        this.setState({openKeys: nextOpenKeys});\r\n    }\r\n\r\n    //IE array.find\r\n    myfilter = (arr1,arr2) => {\r\n        if(arr2.length === 0 || !arr2) {\r\n            return arr1[0];\r\n        }\r\n\r\n        for(var i=0;i<arr1.length;i++)\r\n        {\r\n          if(arr2.indexOf(arr1[i].toString()) === -1)\r\n          {\r\n                return arr1[i];\r\n          }      \r\n        }\r\n        return false;\r\n    }\r\n\r\n    getAncestorKeys = (key) => {\r\n        const map = {\r\n            sub3: ['sub2'],\r\n        };\r\n        return map[key] || [];\r\n    }\r\n\r\n    render() {\r\n        return (\r\n            <Menu\r\n                mode=\"inline\"\r\n                openKeys={this.state.openKeys}\r\n                selectedKeys={[this.state.current]}\r\n                style={{ width: 240 }}\r\n                onOpenChange={this.onOpenChange}\r\n                onClick={this.handleClick}>\r\n                <SubMenu key=\"sub1\" title={<span><span> 1</span></span>}>\r\n                    <Menu.Item key=\"1\"> 1</Menu.Item>\r\n                    <Menu.Item key=\"2\"> 2</Menu.Item>\r\n                    <Menu.Item key=\"3\"> 3</Menu.Item>\r\n                    <Menu.Item key=\"4\"> 4</Menu.Item>\r\n                </SubMenu>\r\n                <SubMenu key=\"sub2\" title={<span><span> 2</span></span>}>\r\n                    <Menu.Item key=\"5\"> 5</Menu.Item>\r\n                    <Menu.Item key=\"6\"> 6</Menu.Item>\r\n                    <SubMenu key=\"sub3\" title=\"\">\r\n                        <Menu.Item key=\"7\"> 7</Menu.Item>\r\n                        <Menu.Item key=\"8\"> 8</Menu.Item>\r\n                    </SubMenu>\r\n                </SubMenu>\r\n                <SubMenu key=\"sub4\" title={<span><span> 3</span></span>}>\r\n                    <Menu.Item key=\"9\"> 9</Menu.Item>\r\n                    <Menu.Item key=\"10\"> 10</Menu.Item>\r\n                    <Menu.Item key=\"11\"> 11</Menu.Item>\r\n                    <Menu.Item key=\"12\"> 12</Menu.Item>\r\n                </SubMenu>\r\n            </Menu>\r\n        )\r\n    }\r\n}\r\n\r\n","desc":" "},{"example":<Demo4 />,"title":" Menu","code":"/**\r\n * @title Menu\r\n * @description \r\n */\r\n\r\nimport React, { Component } from 'react';\r\nimport { Menu } from 'tinper-bee';\r\n\r\nconst SubMenu = Menu.SubMenu;\r\nconst MenuItemGroup = Menu.ItemGroup;\r\n\r\n\r\n\r\nclass Demo4 extends Component {\r\n\r\n    handleClick = (e) => {\r\n        console.log('click', e);\r\n    }\r\n    render() {\r\n        return (\r\n            <Menu onClick={this.handleClick} style={{ width: 240 }} mode=\"vertical\">\r\n                <SubMenu key=\"sub1\" title={<span><span> 1</span></span>}>\r\n                    <MenuItemGroup title=\"Item 1\">\r\n                        <Menu.Item key=\"1\"> 1</Menu.Item>\r\n                        <Menu.Item key=\"2\"> 2</Menu.Item>\r\n                    </MenuItemGroup>\r\n                    <MenuItemGroup title=\"Iteom 2\">\r\n                        <Menu.Item key=\"3\"> 3</Menu.Item>\r\n                        <Menu.Item key=\"4\"> 4</Menu.Item>\r\n                    </MenuItemGroup>\r\n                </SubMenu>\r\n                <SubMenu key=\"sub2\" title={<span><span> 2</span></span>}>\r\n                    <Menu.Item key=\"5\"> 5</Menu.Item>\r\n                    <Menu.Item key=\"6\"> 6</Menu.Item>\r\n                    <SubMenu key=\"sub3\" title=\"Submenu\">\r\n                        <Menu.Item key=\"7\"> 7</Menu.Item>\r\n                        <Menu.Item key=\"8\"> 8</Menu.Item>\r\n                    </SubMenu>\r\n                </SubMenu>\r\n                <SubMenu key=\"sub4\" title={<span><span> 3</span></span>}>\r\n                    <Menu.Item key=\"9\"> 9</Menu.Item>\r\n                    <Menu.Item key=\"10\"> 10</Menu.Item>\r\n                    <Menu.Item key=\"11\"> 11</Menu.Item>\r\n                    <Menu.Item key=\"12\"> 12</Menu.Item>\r\n                </SubMenu>\r\n            </Menu>\r\n        )\r\n    }\r\n}\r\n\r\n","desc":" "},{"example":<Demo5 />,"title":" NavbarMenus","code":"/**\r\n * @title NavbarMenus\r\n * @description navbarmenusPC\r\n *\r\n */\r\n\r\nimport React, { Component } from 'react';\r\nimport { Menu, Navbar, Icon, Badge, FormControl } from 'tinper-bee';\r\n\n\n\n\r\n\r\n\n\r\nconst SubMenu = Menu.SubMenu;\r\nconst MenuItemGroup = Menu.ItemGroup;\r\nconst MenuToggle = Menu.MenuToggle;\r\nconst SideContainer = Menu.SideContainer;\r\n\r\nconst NavItem = Navbar.NavItem;\r\nconst Header = Navbar.Header;\r\nconst Brand = Navbar.Brand;\r\nconst Nav = Navbar.Nav;\r\n\r\n\r\nclass Demo5 extends Component {\r\n    constructor(props, context) {\r\n        super(props, context);\r\n        this.state = {\r\n            expanded: false,\r\n            current: 1,\r\n            searchValue:\"Search\"\r\n        }\r\n    }\r\n\r\n    onToggle = (value) => {\r\n        this.setState({expanded: value});\r\n    }\r\n\r\n    handleClick = (e) => {\r\n        console.log('click ', e);\r\n        this.setState({\r\n            current: e.key\r\n        });\r\n    }\r\n\r\n    onChange = (e) => {\r\n        this.setState({searchValue:e});\r\n    }\r\n\r\n    render() {\r\n        return (\r\n            <div id=\"demo5\">\r\n                <Navbar expanded={this.state.expanded} onToggle={this.onToggle}>\r\n                    <MenuToggle show/>\r\n                    <Header>\r\n                        <Brand>\r\n                            <a href=\"#\"><img style={{width:140}} src=\"http://design.yyuap.com/logos/logox.png\"/></a>\r\n                        </Brand>\r\n                    </Header>\r\n\r\n                    <Nav pullRight>\r\n                        <NavItem eventKey={1}>\r\n                            <FormControl\r\n                                type=\"text\"\r\n                                placeholder=\"Search\"\r\n                                onChange={this.onChange}\r\n                                value={this.state.searchValue}\r\n                            />\r\n                        </NavItem>\r\n                        <NavItem eventKey={2}>\r\n                            <Badge dataBadge=\"4\" colors=\"warning\">\r\n                                <Icon\r\n                                    type=\"uf-bell\"\r\n                                />\r\n                            </Badge>\r\n                        </NavItem>\r\n                        <NavItem eventKey={3}>\r\n                            <Icon type=\"uf-bubble-o\" />\r\n                        </NavItem>\r\n                        <Menu mode=\"horizontal\" className=\"dropdown\">\r\n                            <SubMenu title={<span><Icon type=\"uf-triangle-down\" /></span>}>\r\n                                <Menu.Item key=\"setting:1\"> 1</Menu.Item>\r\n                                <Menu.Item key=\"setting:2\"> 2</Menu.Item>\r\n                                <Menu.Item key=\"setting:3\"> 3</Menu.Item>\r\n                                <Menu.Item key=\"setting:4\"> 4</Menu.Item>\r\n                            </SubMenu>\r\n                        </Menu>\r\n                    </Nav>\r\n                </Navbar>\r\n                <SideContainer\r\n                    onToggle={this.onToggle}\r\n                    expanded={this.state.expanded}>\r\n                    <Menu onClick={this.handleClick}\r\n                          style={{ width: 240 }}\r\n                          defaultOpenKeys={['demo3sub1']}\r\n                          selectedKeys={[this.state.current]}\r\n                          mode=\"inline\"\r\n                        >\r\n                        <SubMenu key=\"demo3sub1\" title={<span><span> 1</span></span>}>\r\n                            <MenuItemGroup title=\" 1\">\r\n                                <Menu.Item key=\"1\"> 1</Menu.Item>\r\n                                <Menu.Item key=\"2\"> 2</Menu.Item>\r\n                            </MenuItemGroup>\r\n                            <MenuItemGroup title=\" 2\">\r\n                                <Menu.Item key=\"3\"> 3</Menu.Item>\r\n                                <Menu.Item key=\"4\"> 4</Menu.Item>\r\n                            </MenuItemGroup>\r\n                        </SubMenu>\r\n                        <SubMenu key=\"demo3sub2\" title={<span><span> 2</span></span>}>\r\n                            <Menu.Item key=\"5\"> 5</Menu.Item>\r\n                            <Menu.Item key=\"6\"> 6</Menu.Item>\r\n                            <SubMenu key=\"demo3sub3\" title=\"\">\r\n                                <Menu.Item key=\"7\"> 7</Menu.Item>\r\n                                <Menu.Item key=\"8\"> 8</Menu.Item>\r\n                            </SubMenu>\r\n                        </SubMenu>\r\n                        <SubMenu key=\"demo3sub4\" title={<span><span> 3</span></span>}>\r\n                            <Menu.Item key=\"9\"> 9</Menu.Item>\r\n                            <Menu.Item key=\"10\"> 10</Menu.Item>\r\n                            <Menu.Item key=\"11\"> 11</Menu.Item>\r\n                            <Menu.Item key=\"12\"> 12</Menu.Item>\r\n                        </SubMenu>\r\n                    </Menu>\r\n                </SideContainer>\r\n            </div>\r\n        )\r\n    }\r\n}\r\n\r\n","desc":" navbarmenusPC"},{"example":<Demo6 />,"title":" ","code":"/**\r\n * @title \r\n * @description \r\n *\r\n */\r\nimport React, { Component } from 'react';\r\nimport { Menu } from 'tinper-bee';\r\n\r\nconst SubMenu = Menu.SubMenu;\r\nconst MenuItemGroup = Menu.ItemGroup;\r\n\r\nclass Demo6 extends Component {\r\n    constructor(props, context) {\r\n        super(props, context);\r\n        this.state = {\r\n            current: 1\r\n        }\r\n    }\r\n\r\n    handleClick = (e) => {\r\n\r\n        this.setState({\r\n            current: e.key,\r\n        });\r\n    }\r\n    \r\n    /**\r\n     * item\r\n     * @param {*} value \r\n     */\r\n    onSelect({item,key,selectedKeys}){ \r\n        console.log(`${key} selected`); //key\r\n        let currentObject = {};\r\n        currentObject.value = item.props.children; //\r\n        currentObject.key = item.props.eventKey;\r\n        console.log(currentObject); \r\n    }\r\n\r\n    render() {\r\n        return (\r\n            <Menu theme=\"dark\" onClick={this.handleClick} style={{ width: 240 }} defaultOpenKeys={['demo3sub1']} selectedKeys={[this.state.current]} mode=\"inline\" onSelect={this.onSelect.bind(this)}>\r\n                <SubMenu key=\"demo3sub1\" title={<span><span> 1</span></span>}>\r\n                    <MenuItemGroup title=\" 1\">\r\n                        <Menu.Item key=\"1\"> 1</Menu.Item>\r\n                        <Menu.Item key=\"2\"> 2</Menu.Item>\r\n                    </MenuItemGroup>\r\n                    <MenuItemGroup title=\" 2\">\r\n                        <Menu.Item key=\"3\"> 3</Menu.Item>\r\n                        <Menu.Item key=\"4\"> 4</Menu.Item>\r\n                    </MenuItemGroup>\r\n                </SubMenu>\r\n                <SubMenu key=\"demo3sub2\" title={<span><span> 2</span></span>}>\r\n                    <Menu.Item key=\"5\"> 5</Menu.Item>\r\n                    <Menu.Item key=\"6\"> 6</Menu.Item>\r\n                    <SubMenu key=\"demo3sub3\" title=\"\">\r\n                        <Menu.Item key=\"7\"> 7</Menu.Item>\r\n                        <Menu.Item key=\"8\"> 8</Menu.Item>\r\n                    </SubMenu>\r\n                </SubMenu>\r\n                <SubMenu key=\"demo3sub4\" title={<span><span> 3</span></span>}>\r\n                    <Menu.Item key=\"9\"> 9</Menu.Item>\r\n                    <Menu.Item key=\"10\"> 10</Menu.Item>\r\n                    <Menu.Item key=\"11\"> 11</Menu.Item>\r\n                    <Menu.Item key=\"12\"> 12</Menu.Item>\r\n                </SubMenu>\r\n            </Menu>\r\n        )\r\n    }\r\n}\r\n\r\n","desc":" "}]


class Demo extends Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false
        }
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        this.setState({open: !this.state.open})
    }

    render() {
        const {title, example, code, desc, scss_code} = this.props;
        let caret = this.state.open ? CARETUP : CARET;
        let text = this.state.open ? "" : "";

        const header = (
            <div>
                {example}
                <Button style={{"marginTop": "10px"}} shape="block" onClick={this.handleClick}>
                    {caret}
                    {text}
                </Button>
            </div>
        );
        return (
            <Col md={12}>
                <h3>{title}</h3>
                <p>{desc}</p>
                <Panel collapsible headerContent expanded={this.state.open} colors='bordered' header={header}
                       footerStyle={{padding: 0}}>
                    <pre><code className="hljs javascript">{code}</code></pre>
                    {!!scss_code ? <pre><code className="hljs css">{scss_code}</code></pre> : null}
                </Panel>
            </Col>
        )
    }
}

class DemoGroup extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <Row>
                {DemoArray.map((child, index) => {

                    return (
                        <Demo example={child.example} title={child.title} code={child.code} scss_code={child.scss_code}
                              desc={child.desc} key={index}/>
                    )

                })}
            </Row>
        )
    }
}

ReactDOM.render(<DemoGroup/>, document.getElementById('tinperBeeDemo'));



